aceholder {\n  font-size: 0.9em;\n  font-weight: 300;\n  font-family: \"Poppins\", sans-serif;\n  color: #aaa;\n}\n\n.form-control::-webkit-input-placeholder {\n  font-size: 0.9em;\n  font-weight: 300;\n  font-family: \"Poppins\", sans-serif;\n  color: #aaa;\n}\n\n.form-control:-ms-input-placeholder {\n  font-size: 0.9em;\n  font-weight: 300;\n  font-family: \"Poppins\", sans-serif;\n  color: #aaa;\n}\n\n.form-control:focus {\n  color: #495057;\n  background-color: #fff;\n  border-color: #33b35a;\n  outline: none;\n  box-shadow: 0 0 0 0.2rem rgba(51, 179, 90, 0.25);\n}\n\n.form-control-label {\n  font-size: .9rem;\n  color: #777;\n  font-weight: 300;\n}\n\nbutton, input[type='submit'] {\n  cursor: pointer;\n  font-family: inherit;\n  font-weight: 300 !important;\n}\n\n.checkbox-template, .radio-template {\n  -webkit-transform: translateY(3px);\n  transform: translateY(3px);\n  -webkit-user-select: none;\n  -moz-user-select: none;\n  -ms-user-select: none;\n  user-select: none;\n  cursor: pointer;\n  position: relative;\n}\n\n.checkbox-template + label, .radio-template + label {\n  margin-left: 10px;\n}\n\n.checkbox-template::before, .radio-template::before {\n  margin-right: 10px;\n  content: '';\n  display: inline-block;\n  -webkit-transform: translate(-2px, -2px);\n  transform: translate(-2px, -2px);\n  width: 18px;\n  height: 18px;\n  line-height: 18px;\n  text-align: center;\n  background: #dae2e7;\n  transition: all 0.2s;\n}\n\n.checkbox-template::after, .radio-template::after {\n  content: '\\F00C';\n  width: 12px;\n  height: 12px;\n  line-height: 12px;\n  text-align: center;\n  display: block;\n  font-family: 'FontAwesome';\n  position: absolute;\n  top: 1px;\n  left: 1px;\n  font-size: 0.7em;\n  opacity: 0;\n  transition: all 0.2s;\n  color: #fff;\n}\n\n.checkbox-template:checked::before, .radio-template:checked::before {\n  background: #33b35a;\n}\n\n.checkbox-template:checked::after, .radio-template:checked::after {\n  opacity: 1;\n}\n\n.radio-template::before {\n  border-radius: 50%;\n  -webkit-transform: translate(-3px, -3px);\n  transform: translate(-3px, -3px);\n}\n\n.radio-template::after {\n  width: 6px;\n  height: 6px;\n  line-height: 6px;\n  text-align: center;\n  position: absolute;\n  top: 3px;\n  left: 3px;\n  border-radius: 50%;\n  content: '';\n}\n\n.radio-template:checked::after {\n  background: #fff;\n}\n\ninput.input-material {\n  width: 100%;\n  border: none;\n  border-bottom: 1px solid #eee;\n  padding: 10px 0;\n}\n\ninput.input-material:focus {\n  border-color: #33b35a;\n}\n\ninput.input-material ~ label {\n  color: #aaa;\n  position: absolute;\n  top: 14px;\n  left: 0;\n  cursor: text;\n  transition: all 0.2s;\n  font-weight: 300;\n}\n\ninput.input-material ~ label.error {\n  color: #dc3545;\n  font-size: 0.75em;\n  position: absolute;\n  top: auto;\n  bottom: -30px;\n  left: 0;\n}\n\ninput.input-material ~ label.active {\n  font-size: 0.8em;\n  top: -10px;\n  color: #33b35a;\n}\n\n.form-group-material {\n  position: relative;\n  margin-bottom: 30px;\n}\n\n.input-group span.input-group-addon {\n  background: #f5f5f5;\n  color: #999;\n  font-weight: 300;\n  border-radius: 0;\n  border: 1px solid #eee;\n}\n\n.input-group .dropdown-toggle {\n  background: #f5f5f5;\n  color: #777;\n}\n\n.input-group .checkbox-template, .input-group .radio-template {\n  -webkit-transform: none;\n  transform: none;\n}\n\n.input-group ul.dropdown-menu {\n  padding: 15px;\n  color: #777;\n  border-radius: 0;\n}\n\n.input-group ul.dropdown-menu a {\n  padding: 5px 0;\n  color: inherit;\n  text-decoration: none;\n}\n\n.input-group ul.dropdown-menu a:hover {\n  color: #33b35a;\n}\n\n.modal-content {\n  border-radius: 0;\n}\n\n.i-checks {\n  display: -webkit-box;\n  display: -ms-flexbox;\n  display: flex;\n}\n\nselect {\n  margin-bottom: 10px;\n}\n\nselect option {\n  color: #999;\n}\n\n/*\n* ==========================================================\n*     FORM PAGE\n* ========================================================